@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto+Condensed:wght@400;700&family=Roboto:wght@300;400;500&display=swap);
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;

}
 
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
 
}
 /* =========== Header Socials =========== */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:0.8 rem;
    gap:0.8 rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

}

.header__socials::after {
   content: '';
   width: 1px;
   height: 2rem;
    background: var(--color-primary);
}

/* ========= Me =========== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding:  5rem 1.5rem 1.5rem 1.5rem;

}
/* =========scroll_down ======== */ 
.scroll__down{
    position: absolute;
    right: 2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* ========== media ========*/
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
    
}

/*=========== media ========*/
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials,
    .scroll__down {
        display: none;
    }
    
}

Nav {
    background: rgba(0,0,0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
    
}

Nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;

}

Nav a:hover {
    background: rgba(0,0,0, 0.3);
}

Nav a.active { 
    background:white;
    color: var(--color-white);
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items:center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);

}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    color: white;

}

.about__card{
   background: var(--color-primary);
   border: 1px solid transparent;
   border-radius: 1rem;
   padding: 2rem;
   text-align: center;
}
.about__card:hover {
    background: transparent;
    border: rgb(44, 43, 43);
    cursor: default;
    color: black;
}
.about__icon{
    color: rgb(85, 85, 85);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font_size: 0.95rem;

}

.about__card small {
    font-size: 0.7rem;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: black;
}


/* ========== media ========*/
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;

    }
    
    .about__content p {
    margin: 1rem 0 1.5rem;
    }
}

/*=========== media ========*/
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;

    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
        .about__content {
            text-align: center;

        }

    .about__content p {
        margin: 1.5rem 0;
    }
    
}

.experience__container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}


.experience__container > div {
    background: rgb(255, 255, 255);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border:1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: black;
    cursor: default;
}   

.experience__container > div h3 {
    text-align:center;
    margin-bottom: 2rem;
    color:var(--color-primary);

}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display:flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}


/* ========== media ========*/
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
  
}

.experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
}

.experience__content {
    padding: 1rem
}
}

/*=========== media ========*/
@media screen and (max-width: 600px) {
.experience__container{
    grid-gap: 1rem;
    gap: 1rem;

}
}
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem

}

.portfolio__item {
    background: black;
    padding: 1.3rem;
    border-radius: 2rem;
    border:1px solid transparent;
    transition: var(--transition);
    color: white;

}
.btn {
    background: var(--color-primary-variant);
   background-color: white;
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
    color:black;
}

.portfolio__item img {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}
.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ========== media ========*/
@media screen and (max-width: 1024px) {
 .portfolio__container{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
 }
}

/*=========== media ========*/
@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    
}

.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap:12%;
    gap:12%;

}

.contact__options {
display: flex;
flex-direction: column;
grid-gap:1.2rem;
gap:1.2rem;
}

.contact__option{
background: var(--color-white);
color: white;
padding: 1.2rem;
border-radius: 1.2rem;
text-align: center;
border: 1px solid transparent !important;
transition: var(--transition);
}
.contact__option:hover {
background: transparent;
border-color: var(--color-primary-variant);

}

.contact__option-icon {
font-size: 1.5rem;
margin-bottom: 0.5rem;
}
.contact__option-icon a{
margin-top: 0.7rem;
display: inline-block;
font-size: 0.8rem;
}

/* ========= FORM ===========*/

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-white);
    resize: none;
    color: var(--color-white);
}


/* ========== media ========*/
@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

/*=========== media ========*/
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
      }
    
}

footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: white;
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;

}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
        background:white;
        color: var(--color-white);
        padding: 0.8rem;
        border-radius: 0.7rem;
        display: flex;
        border: 1px solid transparent;
}

.footer__socials a:hover{
        background: transparent;
        color: white;
        border-color: white;
}
.footer__copyright {
    margin-bottom: 4rem;
    color: white;
}


/*=========== media ========*/
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
    
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #19191a;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #0e0d0d;
    --color-light: rgba(14, 13, 13, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: rgb(248, 246, 246);
    color: #0e0d0d;
    color: var(--color-white);
    line-height: 1.7;
   
 
}

/* =============== General Styles =================================================================*/
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h1, section > h1 {
    text-align: center;
    color: black;
}

section > h2 {
    color:var (--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(14, 13, 13, 0.6);
    color: var(--color-light);
}
a {
    color: #19191a;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #0e0d0d;
    color: var(--color-white);

}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #19191a;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #19191a;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #0e0d0d;
    background: var(--color-white);
    color: white;
    border-color: transparent;
}

.btn-primary {
    background: #19191a;
    background: var(--color-primary);
    color: white;
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* =================== media queries ================= */

@media screen and (max-width: 1024px) {
    .container {
       width: 86%;
       width: var(--container-width-md);
    }

    section > h2 {
        margin-top: 6rem;
    }
}

/* =================== media queries (small) ================= */

@media screen and (max-width: 600px) {
    .container {
       width: 90%;
       width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 2rem;
    }
}



