@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto+Condensed:wght@400;700&family=Roboto:wght@300;400;500&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #19191a;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #0e0d0d;
    --color-light: rgba(14, 13, 13, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: rgb(248, 246, 246);
    color: var(--color-white);
    line-height: 1.7;
   
 
}

/* =============== General Styles =================================================================*/
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h1, section > h1 {
    text-align: center;
    color: black;
}

section > h2 {
    color:var (--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}
a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);

}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: white;
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: white;
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* =================== media queries ================= */

@media screen and (max-width: 1024px) {
    .container {
       width: var(--container-width-md);
    }

    section > h2 {
        margin-top: 6rem;
    }
}

/* =================== media queries (small) ================= */

@media screen and (max-width: 600px) {
    .container {
       width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 2rem;
    }
}


